import React, { useState, useEffect } from 'react';
import { 
  Radar, 
  RadarChart, 
  PolarGrid, 
  PolarAngleAxis, 
  PolarRadiusAxis, 
  ResponsiveContainer, 
  Tooltip 
} from 'recharts';
import { skillsData, categories } from './SkillsData';

const SkillRadarChart = () => {
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [animationStep, setAnimationStep] = useState(0);

  const filteredData = selectedCategory === 'all' 
    ? skillsData 
    : skillsData.filter(skill => skill.category === selectedCategory);

  useEffect(() => {
    const timer = setTimeout(() => setAnimationStep(1), 500);
    return () => clearTimeout(timer);
  }, []);

  const CustomTooltip = ({ active, payload }) => {
    if (active && payload && payload.length) {
      const data = payload[0].payload;
      return (
        <div className="bg-gray-900/95 backdrop-blur-sm text-white p-4 rounded-xl shadow-2xl border border-gray-600">
          <p className="font-bold text-blue-400 text-lg">{data.skill}</p>
          <p className="text-sm">Proficiency: {data.level}%</p>
          <p className="text-sm">Experience: {data.experience}</p>
          <p className="text-xs text-gray-300 capitalize">Category: {data.category}</p>
        </div>
      );
    }
    return null;
  };

  return (
    <div className="w-full max-w-7xl mx-auto p-8 bg-gradient-to-br from-gray-900 via-blue-900/20 to-purple-900/20 rounded-3xl shadow-2xl backdrop-blur-sm border border-gray-700/50">
      {/* Header Section */}
      <div className="text-center mb-12">
        <h2 className="text-5xl font-bold mb-4">
          <span className="bg-gradient-to-r from-blue-400 via-purple-500 to-pink-500 bg-clip-text text-transparent">
            Technical Skills
          </span>
        </h2>
        <p className="text-gray-400 text-xl max-w-2xl mx-auto">
          Interactive visualization showcasing my technical expertise and proficiency levels across different domains
        </p>
      </div>

      {/* Category Filters */}
      <div className="flex flex-wrap justify-center gap-4 mb-12">
        {Object.entries(categories).map(([key, label]) => (
          <button
            key={key}
            onClick={() => setSelectedCategory(key)}
            className={`px-6 py-3 rounded-full text-sm font-semibold transition-all duration-300 transform hover:scale-105 hover:shadow-lg ${
              selectedCategory === key
                ? 'bg-gradient-to-r from-blue-500 to-purple-600 text-white shadow-lg shadow-blue-500/25'
                : 'bg-gray-800/80 text-gray-300 hover:bg-gray-700 hover:text-white border border-gray-600'
            }`}
          >
            {label}
            <span className="ml-2 text-xs opacity-75">
              ({selectedCategory === key ? filteredData.length : skillsData.filter(s => s.category === key).length})
            </span>
          </button>
        ))}
      </div>

      {/* Radar Chart */}
      <div className="mb-12">
        <ResponsiveContainer width="100%" height={600}>
          <RadarChart data={filteredData} margin={{ top: 60, right: 100, bottom: 60, left: 100 }}>
            <PolarGrid 
              stroke="#374151" 
              strokeWidth={1}
              className="opacity-60"
            />
            <PolarAngleAxis 
              dataKey="skill" 
              tick={{ fill: '#D1D5DB', fontSize: 14, fontWeight: 500 }}
              className="text-sm"
            />
            <PolarRadiusAxis 
              angle={0} 
              domain={[0, 100]} 
              tick={{ fill: '#9CA3AF', fontSize: 12 }}
              tickCount={6}
              stroke="#4B5563"
            />
            <Radar
              name="Skills"
              dataKey="level"
              stroke="url(#skillGradient)"
              fill="url(#skillGradient)"
              fillOpacity={0.25}
              strokeWidth={3}
              dot={{ fill: '#60A5FA', strokeWidth: 3, r: 8 }}
              animationBegin={0}
              animationDuration={2000}
              isAnimationActive={animationStep === 1}
            />
            <Tooltip content={<CustomTooltip />} />
            <defs>
              <linearGradient id="skillGradient" x1="0%" y1="0%" x2="100%" y2="100%">
                <stop offset="0%" stopColor="#3B82F6" />
                <stop offset="50%" stopColor="#8B5CF6" />
                <stop offset="100%" stopColor="#EC4899" />
              </linearGradient>
            </defs>
          </RadarChart>
        </ResponsiveContainer>
      </div>

      {/* Skills Grid */}
      <div className="mb-12">
        <h3 className="text-2xl font-bold text-white mb-8 text-center">Detailed Breakdown</h3>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
          {filteredData.map((skill) => (
            <div
              key={skill.skill}
              className="bg-gray-800/60 backdrop-blur-sm rounded-xl p-6 border border-gray-700/50 hover:border-gray-600 hover:bg-gray-800/80 transition-all duration-300 transform hover:scale-105 hover:shadow-xl"
            >
              <div className="flex items-center justify-between mb-3">
                <h4 className="font-semibold text-white text-lg">{skill.skill}</h4>
                <span className="text-blue-400 text-lg font-bold">{skill.level}%</span>
              </div>
              <div className="w-full bg-gray-700 rounded-full h-3 mb-3">
                <div
                  className="h-3 rounded-full transition-all duration-1500 ease-out shadow-sm"
                  style={{
                    width: animationStep === 1 ? `${skill.level}%` : '0%',
                    background: `linear-gradient(90deg, ${skill.color}, ${skill.color}BB)`
                  }}
                />
              </div>
              <div className="flex justify-between text-sm">
                <span className="text-gray-400 capitalize">{skill.category}</span>
                <span className="text-gray-300">{skill.experience}</span>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Statistics */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="bg-gradient-to-br from-blue-600/20 to-blue-800/20 rounded-xl p-6 text-center border border-blue-500/30">
          <div className="text-3xl font-bold text-blue-400 mb-2">
            {Math.round(filteredData.reduce((acc, skill) => acc + skill.level, 0) / filteredData.length)}%
          </div>
          <div className="text-gray-400 text-sm">Average Proficiency</div>
        </div>
        <div className="bg-gradient-to-br from-purple-600/20 to-purple-800/20 rounded-xl p-6 text-center border border-purple-500/30">
          <div className="text-3xl font-bold text-purple-400 mb-2">{filteredData.length}</div>
          <div className="text-gray-400 text-sm">Total Skills</div>
        </div>
        <div className="bg-gradient-to-br from-pink-600/20 to-pink-800/20 rounded-xl p-6 text-center border border-pink-500/30">
          <div className="text-3xl font-bold text-pink-400 mb-2">
            {filteredData.filter(skill => skill.level >= 85).length}
          </div>
          <div className="text-gray-400 text-sm">Expert Level (85%+)</div>
        </div>
        <div className="bg-gradient-to-br from-green-600/20 to-green-800/20 rounded-xl p-6 text-center border border-green-500/30">
          <div className="text-3xl font-bold text-green-400 mb-2">
            {new Set(filteredData.map(skill => skill.category)).size}
          </div>
          <div className="text-gray-400 text-sm">Skill Categories</div>
        </div>
      </div>
    </div>
  );
};

export default SkillRadarChart;
